.about-section {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }

  .profile {
    width: 200px;
    border-radius: 2%;
    margin-right: 40px;
    margin-bottom: 40px;
  }
  
  .profile-photo {
    width: 100%;
  }
  
  .about-text {
    text-align: justify;
    max-width: 600px;
  }
  
  .about-text h2 {
    margin-bottom: 20px;
  }